import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import image from './image/gamysh.jpg';
import image1 from './image/hotel.jpg'
import image2 from './image/hotel1.jpg'
import image3 from './image/hotel2.jpeg'
import { Icon } from 'react-icons-kit'
import {smallDown} from 'react-icons-kit/entypo/smallDown';
import {heartO} from 'react-icons-kit/fa/heartO'
import {mapMarker} from 'react-icons-kit/fa/mapMarker'
import {ic_room} from 'react-icons-kit/md/ic_room'
import {ic_check} from 'react-icons-kit/md/ic_check'
import {ic_close} from 'react-icons-kit/md/ic_close'
import {ic_swap_vert} from 'react-icons-kit/md/ic_swap_vert'
import {map} from 'react-icons-kit/fa/map'
import {ic_tune} from 'react-icons-kit/md/ic_tune'
import {ic_delete} from 'react-icons-kit/md/ic_delete';
import {useDispatch, useSelector} from 'react-redux';
import { useForm, Controller} from "react-hook-form";
import { post, fetchData } from '../application/middlewares/index';
import { getCategory, getToken } from '../application/selectors/auth';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";



const Navbar = lazy(() => import('./Navbar'))
const Map = lazy(() => import('./Map'))
const Footer = lazy(() => import('./Footer'))



function Notice(){
  const { lang, dispatchLang } = useContext(LangContext);
    

    const { register ,watch, handleSubmit, formState: { errors }, setError, setValue, getValues} = useForm({
        // resolver: yupResolver(schema)

    });
    const history = useHistory();
    setValue("descriptions.0.language_id", 1);
    setValue("descriptions.1.language_id", 2);
    const [main_id, setMain_id] = useState();
    // const [category_id, setCategory_id] = useState();
    // const [type_id, setType_id] = useState();
    const [forms, setForms] = useState();
    const [images, setImages] = useState(null);
    // const [specifications, setSpecifications] = useState([]);
    // const [value, setValue] = useState([]);
    // const [values, setValues] = useState([]);
    const [filter, setFilter] = useState([]);
    // const price=325;
    // const area=1235;
    // const position={"lng":51.251, "lat":52.625};
    // const location_id=8;
    // const descriptions= [
    //     {"language_id":1, "description":"something about this home what you need to knowTm"},
    //     {"language_id":2, "description":"something about this home what you need to knowRU, "}
    // ]
    const watchType = watch("type_id")

    const categories = useSelector(getCategory);
    const token1 = useSelector(getToken);
    const dispatch = useDispatch();
    // console.log(token1,"---token");

    // const formdata = new FormData();

    const ChangeImage = (e) =>{
        setImages([]);
        setImages(e.target.files)
        // console.log(images)
        // setImages(e.target.files[0])
        // setImages(e.target.files)
    }
    // console.log(categories);
    // setValues(prevState => [...prevState, item_value.id])
    const onSubmit = (data1) => {
        data1={...data1,"specifications":filter, "location_id":4, "position":{"lng":51.251, "lat":52.625}}
        console.log(data1,"----useForm hook");
        dispatch(post({
            url:`user/tm/add-real-estate`,
            token: token1,
            data : data1,
            action:(response)=>{
                console.log(response)
                if(response.success){
                    const image_id = response.data.rows.id;
                    const formdata = new FormData();
                    for(let i = 0; i<images.length; i++){
                        formdata.append("picture", images[i])
                    }
                    dispatch(post({
                        url:`user/tm/add-real-estate-images/${image_id}`,
                        token: token1,
                        data: formdata,
                        action:(response)=>{
                        if(response.success){
                        console.log("succes ",response);
                        history.push('/')

                        } else {
                        console.log("error image data ",response);
                    }
                }
                    }))
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }
    

    useEffect(()=>{
        if (getValues("type_id")) {
        dispatch(fetchData({
            url:`tm/specifications-for-type/${getValues("type_id")}/${getValues("category_id")}`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("get_form ",response)
                    // console.log(response.data.rows);
                    setForms(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))}
    },[watchType])

    useEffect(()=>{
        setFilter([]);
        for(let i=0; i<forms?.length; i++){
            setFilter(prevState => [...prevState, {"id":forms[i].specification_id, "is_required":forms[i].is_required, "is_multiple":forms[i].is_multiple, values:[]}])
        }
    }, [forms])
    // console.log(filter);
    function Valuee(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                item.values[0]=value1;
            }
            return item;
        }))
        // console.log(value,"----1");
        // console.log(filter,"---1");
    }
    function Values_more(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                if(item.values.includes(value1)){
                    item.values = item.values.filter(item1 =>item1 !== value1)
                }else{
                    item.values.push(value1);
                }
            }
            return item;
        }))
        // console.log(value,"----2");
        // console.log(value1,"---2");
        // console.log(filter,"---2");
    }
    
    return(
        <main>
            <Navbar />
            <div className="main pt-5 border-t bg-gray-100">
                <div className="header_text p-8">
                    <h1 className="text-3xl font-medium mb-4">{Lang1[lang].not1}</h1>
                    <p>{Lang1[lang].not2}</p>
                    <p>{Lang1[lang].not3} <Link to="" className="text-blue-600">Gamysh.com</Link>{Lang1[lang].not4}  <Link to="" className="text-blue-600">{Lang1[lang].not5}</Link></p>
                </div>
            </div>
            <div className="main py-5 bg-gray-100">
                <form onSubmit={handleSubmit(onSubmit)} className="header_text bg-white p-8">
                    <p className="font-medium">{Lang1[lang].not6}</p>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not7}</p>
                        </div>
                        <div className="filter_links1 flex">
                        <label onClick={(e)=>{
                            setValue("category_id", 1);
                            if(document.getElementById('label').checked) {
                            let q= e.target.parentElement.classList.add("toolbar");
                            document.getElementById('label1').parentElement.classList.remove("toolbar");
                            }
                            // let q= e.target.parentElement.classList.add("toolbar");
                            // console.log(e.target.parentElement,"--1")
                        }}
                         className="flex items-center 
                        radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                        hover:text-white hover:bg-blue-600 mr-4" for="label">
                            <input type="radio" className="hidden"  name="radio" id="label" />
                            <p>{Lang1[lang].not8}</p>
                        </label>
                            <label  onClick={(e) =>{
                            setValue("category_id", 2);
                            if(document.getElementById('label1').checked) {
                            let q= e.target.parentElement.classList.add("toolbar");
                            document.getElementById('label').parentElement.classList.remove("toolbar");
                            }
                        }}  
                            className="flex items-center
                            radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                            hover:text-white hover:bg-blue-600 mr-4" for="label1">
                            <input type="radio" className="hidden" name="radio" id="label1" />
                            <p>{Lang1[lang].not9}</p>
                            </label>
                        </div>
                    </div>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not10}</p>
                        </div>
                        <div className="filter_links1 flex">
                        <label className="flex items-center
                        radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                        hover:text-white hover:bg-blue-600 mr-4" for="label2">
                            <input type="radio" onClick={(e) => {
                                setMain_id(1);
                                if(document.getElementById('label2').checked) {
                                    let q= e.target.parentElement.classList.add("toolbar");
                                    document.getElementById('label3').parentElement.classList.remove("toolbar");
                                    }
                            }} className="hidden" name="radio1" id="label2" />
                            <p>{Lang1[lang].not11}</p>
                        </label>
                        <label className="flex items-center
                        radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                        hover:text-white hover:bg-blue-600" for="label3">
                            <input type="radio" onClick={(e) => {
                                setMain_id(0);
                                if(document.getElementById('label3').checked) {
                                    let q= e.target.parentElement.classList.add("toolbar");
                                    document.getElementById('label2').parentElement.classList.remove("toolbar");
                                    }
                                }} className="hidden" name="radio1" id="label3" />
                            <p>{Lang1[lang].not12}</p>
                        </label>
                        </div>
                    </div>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not13}</p>
                        </div>
                        <div className="filter_links1">
                             
                            <div>
                                {categories[getValues("category_id")-1]?.main_types?.map((item_main)=>(
                                    main_id == 1 ?
                                    <div>
                                        {item_main?.sub_types?.map((item_sub)=>(
                                            item_main.id == 1 ?
                                            <label className="flex items-center mr-5" for={item_sub.name}>
                                            <input type="radio" className="mr-3" onClick={() => setValue("type_id" , item_sub.id)} id={item_sub.name} name="radio2"  />
                                            <p className="mr-5" key={item_sub.id}>{item_sub.name}</p>
                                            </label>
                                            :
                                            null
                                        ))}
                                    </div>
                                    :
                                    main_id == 0 ?
                                    <div>
                                        {item_main?.sub_types?.map((item_sub)=>(
                                            item_main.id == 2 ?
                                            <label className="flex items-center mr-5" for={item_sub.name}>
                                            <input type="radio" className="mr-3" onClick={() => setValue("type_id" , item_sub.id)} id={item_sub.name} name="radio2"  />
                                            <p className="mr-5" key={item_sub.id}>{item_sub.name}</p>
                                            </label>
                                            :
                                            null
                                        ))}
                                    </div>
                                    :
                                    null
                                      
                                ))}
                            </div>
                            
                        </div>
                    </div>
                    <hr className="my-3"/>
                    {/* <div  className="w-full"> */}
                    <p className="font-medium my-1"> {Lang1[lang].not14}</p>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not15}</p>
                        </div>
                        <div className="filter_links1">
                        <input type="text" {...register("price")} name="price"
                        className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                        <span>{Lang1[lang].not16}</span>
                        </div>
                    </div>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not17}</p>
                        </div>
                        <div className="filter_links1">
                            <input type="text" {...register("area")} name="area"
                            className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                            <span>м²</span>
                        </div>
                    </div>
                    {forms?.map((item)=>(
                        <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p key={item.id}>{item.name}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="flex flex-wrap">
                                {item.is_multiple ?
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                <label className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                hover:text-white hover:bg-blue-600 mr-4" for={item_value.value_id} >
                                <input type="checkbox" name={item.name} id={item_value.value_id} className="hidden" onClick={(e) =>{
                                    Values_more(item.specification_id,item_value.value_id);
                                    var selects = document.querySelectorAll(".radio-toolbar");
                                    selects.forEach(function(item) {
                                        // console.log(selects[item]);
                                        if(item.children[0].checked) {
                                            let q= item.classList.add("toolbar");
                                            } else {
                                                let q= item.classList.remove("toolbar");
                                            }
                                      });
                                }  } />
                                <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                </label>
                                ))}
                                </div>
                                :
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                <label className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                hover:text-white hover:bg-blue-600 mr-4" for={item_value.value_id}>
                                <input type="radio" name={item.name} id={item_value.value_id} className="hidden" onClick={() =>{
                                    Valuee(item.specification_id,item_value.value_id);
                                    var selects = document.querySelectorAll(".radio-toolbar");
                                    selects.forEach(function(item) {
                                        // console.log(item.children[0]);
                                        if(item.children[0].checked) {
                                            let q= item.classList.add("toolbar");
                                            } else {
                                                let q= item.classList.remove("toolbar");
                                            }
                                      });
                                }} />
                                <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                </label>
                                ))}
                                </div>
                                
                                }
                                
                            </div>
                        </div>
                    </div>
                    ))}
                    
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not18}</p>
                        </div>
                        <div className="filter_links1">
                            <textarea {...register("descriptions.0.description")} 
                            className="border p-1 w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                        </div>
                    </div>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not19}</p>
                        </div>
                        <div className="filter_links1">
                            <textarea {...register("descriptions.1.description")}
                            className="border p-1 w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                        </div>
                    </div>
                    <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p>{Lang1[lang].not20}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="w-1/2 h-40 border flex justify-center items-center">
                                <input type="file" onChange={ChangeImage} multiple />
                            </div>
                            <p className="text-sm text-gray-600">{Lang1[lang].not21}</p>
                        </div>
                    </div>
                    <hr className="my-8"/>
                    <p className="font-medium my-3">{Lang1[lang].not22}</p>
                    <input type="text" placeholder="Turkmenistan, Ashgabat" className="p-1 px-2 border rounded-sm mb-3" />
                    <Map />
                    <button  className="btn1 font-medium w-40 text-sm mt-5 flex self-center" type="submit">{Lang1[lang].not23}</button>
                    {/* </div> */}
                </form>
            </div>
            <Footer />
        </main>

    )
}

export default Notice;